<template>
    <div class="top-search-box">
        <i class="iconfont icon-xiaoxizhongxin"></i>
        <div  class="search" @click="tosearch()">
            <i class="iconfont icon-sousuo"></i>
            <span class="searchword">{{keyword}}</span>
        </div>
        <i class="iconfont icon-gouwuche"></i>
    </div>
  
</template>
<script>
import {mapGetters} from 'vuex'
export default {
    computed:{
        ...mapGetters([
            "keyword"
        ])
    },
    methods:{
        tosearch(){
            this.$store.dispatch("tosearch");
            console.log(1);
        }
    },
    mounted(){
            this.$store.dispatch("gethot");
    }
  
}
</script>
<style lang="less" scoped>
    .top-search-box{
        background: white;
        width: 100%;
        height: 0.8rem;
        background:white;
        text-align: center;
        border-bottom: 1px solid #eee;
        color: #999;
        display: flex;
        align-items: center;
        justify-content:space-around;
        .icon-xiaoxizhongxin{
            margin-left:0.13rem; 
        }
        .icon-gouwuche{
            margin-right:0.13rem;
        }
        .search{
            background: #eaeaea;
            outline: none;
            border: none;
            width: 70%;
            height: 0.53rem;
            border-radius: 4px;
            font-size: 0.2rem;
            text-align: left; 
            line-height: 0.48rem;
            .icon-sousuo{
                font-size: 0.3rem;
                margin-left: 0.15rem;
                vertical-align: middle;
            }

        }
    }

</style>



