<template>
  <div class="cart-box">
    <div class="top-search-box">
        <i class="iconfont icon-xiangzuojiantou" @click="$router.go(-1)"></i>
        <div  class="title">
            尚未登录
        </div>
        <div class="right-p">
          <i class="iconfont icon-xiaoxizhongxin"></i>
        </div>
    </div>
    <div class="empty">
    <div class="empty-box">
        <div class="cart-i">
          <img src="../../../assets/weixin.jpg" alt="">
        </div>
        <p>登陆后查看详情~</p>
        <router-link to="/" tag="div" class="gohome">
            请登录
        </router-link>
    </div>
</div>
  </div>
</template>
<script>
export default {
  mounted(){
      this.$store.dispatch("hiddennav");
    }
}
</script>

<style lang="less" scoped>
  .cart-box{
    width: 100%;
     .top-search-box{
        background: white;
        height: 0.8rem;
        background:white;
        padding-left:0.15rem;
        text-align: center;
        border-bottom: 1px solid #ccc;
        color: #666;
        display: flex;
        align-items: center;
        justify-content:space-between;
        .icon-xiangzuojiantou{
            margin-right:0.13rem; 
        }
        .title{
            font-size: 0.3rem;
            text-align: center; 
            font-weight: 800;
        }
        .right-p{
          margin-right:0.2rem;
          color:#777;
          display: flex;
          align-items: center;
          .bianji{
            font-size:0.22rem;
            margin-right: 0.1rem;
          }
        }

      }
    .empty{
        width: 100%;
        height: 9.4rem;
        display: flex;
        align-items: center;
        text-align: center;
        .empty-box{
          width: 100%;
          text-align: center;
          .cart-i{
            margin: 0 auto;
            width: 2.2rem;
            height: 2.2rem;
            line-height: 2.2rem;
            background: #d5cbce;
            color: white;
            border-radius: 1.25rem;
            box-shadow: 1px 1px 1px #ccc;
            overflow: hidden;
            img{
              width: 100%;
            }
          }
          p{
            font-size: 0.22rem;
            color:#777;
            margin-top:0.24rem;
          }
          .gohome{
            background:rgba(100,255,30,0.8);
            color: white;
            height: 0.6rem;
            width: 1.6rem;
            margin:0.2rem auto;
            border-radius: 0.1rem;
            line-height: 0.6rem;
            font-size: 0.26rem;
          }
        }
      }
  
  }

</style>



