<template>
    <div class="detailbox">
        <div class="gohome"  @click="$router.go(-1)">
            <i class="iconfont icon-xiangzuojiantou"></i>
        </div>
        <div class="bigimg" v-if="imgflag"  @click="leaveimg()">
                <div class="wrap">
                    <swiper :options="swiperOption" ref="mySwiper">
                            <swiper-slide v-for="(item,i) in imgbanner" :key="i" :class="index==i?'swiper-slide-active':''">
                                <img :src="item" alt="" class="imgsbox">
                            </swiper-slide>
                            <div class="swiper‐pagination" slot="pagination"></div> 
                            <div class="swiper‐button‐prev" slot="button‐prev"></div> 
                            <div class="swiper‐button‐next" slot="button‐next"></div>
                            <div class="swiper‐scrollbar" slot="scrollbar"></div>
                    </swiper>
                </div>
        </div>
        <div class="introbox">
            <div class="imgbox">
                <img :src="pro.img||(pro.showLarge?pro.showLarge.img:'')" alt="">
            </div>
           <div class="introtext">
               <p class="title">{{pro.title}}</p>
               <div class="price">
                   <p class="symbol" v-if="!pro.showLarge">¥</p>
                   <p class="newprice">{{pro.price}}</p>
                   <p class="oldprice">¥{{pro.orgPrice}}</p>
               </div>
               <p class="freemail">免邮费</p>
               <div class="benifit">
                   <p>品牌认证</p>
               </div>
           </div>
        </div>
        <div class="comment">
            <p class="comment-title">买家评价<span> {{comment.total}}</span>｜销售<span> {{pro.sale}}</span></p>
            <div class="keywords">
                <p class="keyword" v-for="(item ,i ) in comment.rateTags" v-if="i!=0" :key="i">{{item.value}}</p>
            </div>
            <div class="comment-box">
                <div class="comment-list" v-for="(content,i) in comment.list" :key="i">
                        <div class="mybox">
                            <p class="auto">
                                <img :src="content.user.avatar" alt="">
                                <span> {{content.user.uname}}</span> 
                            </p>
                            <p class="content">{{content.content}}</p>
                            <p class="comment-time">{{content.time}}</p>
                            <div class="comment-img" v-if="content.images">
                                <img v-for="(img,i) in content.images" :key="'img'+i" :src="img" @click="bigimg(content.images,i)">
                            </div>
                        </div>
                        
                        <div v-if="content.explain" class="explain">
                            {{content.explain}}
                        </div>
                </div>
            </div>
            
        </div>
        <div class="addcart">
            <div class="botton-box" :style="'color:'+(love.indexOf(pro.iid)!=-1?'#fb2b79':'')" @click="addlove(pro.iid)">
                <i class="icon iconfont icon-shoucang"></i>
                <p class="botton-text">收藏</p>
            </div>
            <div class="botton-box"  @click="addcart(pro.iid,pro.title,pro.price,pro.orgPrice,pro.shopSeller,pro.img||pro.showLarge.img)">
                <i class="icon iconfont icon-gouwuche"></i>
                <p class="botton-text">加购</p>
                <p class="success" ref="succ">添加成功</p>
            </div>
            <div class="tobuy">立即购买</div>
        </div>
        
    </div>

  
</template>
<script>
import { mapGetters } from "vuex"
export default {
    data(){
        return {
            pro:{},
            imgbanner:[],
            index:0,
            imgflag:false,
            swiperOption:{ 
                // autoplay:1600,
                // loop:true,
                pagination : '.swiper‐pagination', 
                paginationClickable :true, 
                prevButton:'.swiper‐button‐prev', 
                nextButton:'.swiper‐button‐next', 
                scrollbar:'.swiper‐scrollbar'

            }
        }
    },
    computed:{
        ...mapGetters([
            "comment",
            "love"
        ])
    },
    mounted(){
        let pro = this.$route.params.pro;
        this.pro=pro;
        this.$store.dispatch("hiddennav");
        this.$store.dispatch("getcommit",pro.iid);
        document.documentElement.scrollTop=0;
    },
    methods:{
        bigimg(images){
            this.imgbanner=images;
            this.index=0;
            this.imgflag=true;
        },
        leaveimg(){
            this.imgflag=false;
        },
        addcart(id,title,newprice,oldprice,store,img){
            let content={
                id,
                title,
                newprice,
                oldprice,
                seller:store,
                img,
                choose:false,
                count:1
            }
            this.$store.dispatch("addcarts",content);
            // 出现成功
            this.$refs.succ.style.opacity=1;
            setTimeout(()=>{
                this.$refs.succ.style.opacity=0;
            },500)
        },
        addlove(id){
            this.$store.dispatch("addlove",id);
        }
    }
}
</script>
<style lang="less" scoped>
.detailbox{
    width: 100%;
    .gohome{
        width:0.65rem;
        height:0.65rem;
        background:white;
        border-radius:0.35rem;
        top:0.18rem;
        left:0.18rem;
        position: fixed;
        text-align: center;
        opacity: 0.4;
        line-height: 0.22rem;
        .icon-xiangzuojiantou{
            font-size: 0.4rem;
        }
    }
    .bigimg{
        width: 100%;
        height: 11.4rem;
        position:fixed;
        display: flex;
        align-items: center;
        top:0;
        z-index: 9999;
        background:rgba(0,0,0,0.7);
        .wrap{
            width: 100%;
            height:4.6rem;
            // margin-top:3.2rem; 
            overflow: hidden;
            .imgsbox{
                width: 100%;
            }
            .swiper-pagination-bullets{
                position: relative;
                top:-5.5rem;
                z-index: 99999;
                left: 2.7rem;;
            }
        }
        .fade-enter-active,
        .fade-leave-active {
            transition: width 2s
        }
        
        .fade-enter,
        .fade-leave-to {
            width:0%
        }

    }
    .introbox{
        background: white;
        width: 100%;
        height: 11.14rem;
        .imgbox{
            width: 100%;
            overflow: hidden;
            height: 8.3rem;
            img{
                width: 100%;
            }
        }
        .introtext{
            padding: 0.2rem 0.23rem;
            font-size: 0.28em;
            .title{    
                font-weight: 500;
                color:#444;
                height: 0.68rem;
                line-height: 0.34rem;
                overflow: hidden;
            }
            .price{
                margin-top:0.15rem;
                height: 0.8rem;
                p{
                    float:left;
                }
                .symbol{
                    height: 0.3rem;
                    font-size:0.22rem;
                    color:#fb2b79;
                }
                .newprice{
                    height: 0.8rem;
                    line-height: 0.54rem;
                    font-size: 0.58rem;
                    font-weight: bold;
                    color:#fb2b79;
                }
                .oldprice{
                    margin-left: 0.10rem;
                    line-height: 0.54rem;
                    height: 0.8rem;
                    text-decoration:line-through;
                    font-size: 0.20rem;
                    font-weight: 700;
                    color:#777;
                }
            }
            .freemail{
                font-size: 0.2rem;
                color:#999;
                padding-left: 0.08rem;
                padding-bottom: 0.15rem;
                border-bottom: 1px solid #eee;
            }
            .benifit{
                font-size: 0.21rem;
                padding-left: 0.08rem;
                color:#555;
                padding-top: 0.15rem;
                display: flex;
                justify-content: space-between;
                p{
                    line-height: 0.22rem;
                }
            }
        }
    }
    .addcart{
        width: 100%;
        box-sizing: border-box;
        border-top:1px  solid #ddd;
        height: 0.87rem;
        position:fixed;
        bottom: 0;
        display: flex;
        background: white;
        .botton-box{
            color:#666;
            width: 25%;
            box-sizing: border-box;
            text-align: center;
            border-right:1px  solid #ddd;
            font-size: 0.2rem;
            padding-top:0.08rem;
            .botton-text{
                height: 0.36rem;
                line-height: 0.36rem;
            }
            .success{
                position: relative;
                top:-1.08rem;
                font-size: 0.24rem;
                left:0.9rem;
                color:#fb2b79;
                background: rgba(252, 192, 192, 0.5);
                border-radius: 0.4rem;
                width: 1.2rem;
                font-weight: 900;
                z-index: 9999;
                opacity: 0;
            }
        }
        .tobuy{
            background: #fb2b79;
            width: 50%;
            box-sizing: border-box;
            text-align: center;
            color:white;
            height: 0.87rem;
            line-height: 0.87rem;
            font-size: 0.34rem;
        }
    }
    .comment{
        overflow: hidden;
        width: 100%;
        background: white;
        margin-top:0.13rem;
        margin-bottom: 1rem;
        border:1px solid #eee;
        padding: 0.18rem 0.22rem;
        .comment-title{
           font-size: 0.20rem;
           color:#999;
           font-weight: bold;
        }
        .keywords{
            margin-top: 0.06rem;
            width: 90%;
            line-height: 0.5rem;
            display: flex;
            flex-wrap:wrap;
            overflow: hidden;
            padding-top:0.2rem;
            font-size: 0.21rem;
            font-weight: 450;
            margin-bottom: 0.15rem; 
            .keyword{
                height: 0.4rem;
                text-align: center;
                margin-top:0.08rem;
                color:rgba(255,0,0,0.9);
                padding: 0.01rem 0.1rem;
                margin-right: 0.1rem; 
                line-height: 0.4rem;
                background: #fee1ea;
            }
        }
        .comment-box{
            .comment-list{
                width: 100%;
                .mybox{
                    width: 92%;
                    border-bottom: 1px dashed #eee;
                    padding-bottom:0.1rem; 
                    .auto{
                        height: 0.8rem;
                        line-height: 0.6rem;
                        width: 90%;
                        display: flex;
                        align-items: center;
    
                        img{
                            width: 0.6rem;
                            height: 0.6rem;
                            border-radius: 0.3rem;
                            vertical-align: middle;
                            margin-right:0.15rem; 
                        }
                        span{
                            color:#444;
                            font-size: 0.21rem;
                        }
                    }
                    .content{
                        margin-top:0.2rem; 
                        font-size: 0.22rem;
                        color:#666;
                        line-height: 0.36rem;
                    }
                    .comment-time{
                        font-size: 0.16rem;
                        color:#999;
                        height: 0.15rem;
                        line-height: 0.rem;
                        margin-top: 0.2rem;
                        font-weight: bold;
                    }
                    .comment-img{
                        margin-top:0.13rem; 
                        line-height: 1.28rem;
                        width: 90%;
                        img{
                            width: 1.224rem;
                            height: 1.224rem;
                            margin-right:0.1rem; 
                        }
                    }

                }
                .explain{
                    width: 92%;
                    padding-top:0.21rem;
                    padding-bottom: 0.21rem;
                    padding-right:0.2rem;   
                    font-size: 0.2rem;
                    border-bottom: 1px dashed #eee;
                    color:#888;
                }
                
            }

        }  
    }

}

</style>






