<template>
    <div>
        <p class="header-box" v-if="typeheader.length != 0">
            <img class="header" :src="typeheader[0].image">
        </p>
        <div class="type-box">
            <div v-for="(item,i) in type" :key="i" class="realtype">
                <div class="img-box">
                    <img :src="item.image" alt="">
                </div>
                <p class="img-title">{{item.title}}</p>
            </div>
        </div>
    </div>
  
</template>
<script>
import {mapGetters} from "vuex"
export default {
    computed:{
        ...mapGetters([
            "type",
            "typeheader"            
        ])
    },
    methods:{
      getcontent(){
          this.$store.dispatch("lodetypedetail")
      }
    },
    mounted(){
        this.getcontent();
    }
}
</script>
<style lang="less" scoped>
.header-box{
    width: 100%;
    text-align: center;
    .header{
        width: 93%;
        margin-top: 0.23rem;
    }
}
.type-box{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    .realtype{
        width: 33.3%;
        box-sizing: border-box;
        height: 1.9rem;
        text-align: center;
        font-size: 0.21rem;
        color:#666;
        .img-box{
            margin: 0.18rem auto;
            width: 70%;
            height: 1.1rem;
            background: #ddd;
            overflow: hidden;

            img{
                width: 100%
            }
        }

    }

}


</style>





