<template>
  <div>
    <TopSearch></TopSearch>
    <div class="content-box">
      <TypeTitles></TypeTitles>
      <TypeRightContent></TypeRightContent>
    </div>
      
  </div>
</template>
<script>
import TopSearch from "../HomeComponet/HomeChildren/TopSearch/TopSearch"
import TypeTitles from "../TypeComponent/TypeChildrens/TypeTitles/TypeTitles"
import TypeRightContent from "../TypeComponent/TypeChildrens/TypeRightContent/TypeRightContent"
export default {
  components:{
    TopSearch,
    TypeTitles,
    TypeRightContent
  },
  mounted(){
    this.$store.dispatch("shownav");
  }
}
</script>

<style lang="less" scoped>
  div{
    width: 100%;
    .content-box{
      width: 100%;
      display: flex;
      }
    }

</style>



