<template>
    <div>
        <div class="title-box">
            <div v-for="(item,i) in sortfilter" :class="i==redkey?'red':''" :key="i" class="sort-title" @click="loadsort(item.sortKey,productkey,i)">
                <p>
                {{item.title}}
                </p>
            </div>
        </div>    
        <div class="pro-box">
            <router-link :to="{name:'detail',params:{pro}}" tag="div"  v-for="(pro,i) in products" :key="'pro'+i" class="sma-box">
                <div class="img-box">
                    <img :src="pro.img">
                </div>
                <p class="title">{{pro.title}}</p>
                <p class="love">{{pro.cfav}}</p>
                <p class="price">¥{{pro.price}}</p>
                <p class="sale">已售{{pro.sale}}件</p>
            </router-link>
        </div>
    </div>
  
</template>
<script>
import {mapGetters} from "vuex"
export default {
    data(){
        return {
            redkey:0
        }
    },
    computed:{
        ...mapGetters([
            "sortfilter",
            "products",
            "productkey",
        ])
    },
    methods:{
        getcontent(){
            this.$store.dispatch("lodeproductdetail");
        },
    //   重新排序
        loadsort(sortKey,productkey,i){
            let params={
                cKey:"h5-cube",
                fcid:productkey,
                sort:sortKey
            }
            this.$store.dispatch("lodeproductdetail",params)
            // 改变 颜色
            this.redkey=i;
        }
    },
    mounted(){
        this.getcontent();
    },
  
}
</script>
<style lang="less" scoped>
.title-box{
    display: flex;
    font-size: 0.24rem;
    color:#333;
    height: 0.63rem;
    padding-top:0.15rem; 
    border-bottom: 1px solid #ddd;

    .sort-title{
        width: 33%;
        text-align: center;
        height: 100%;
        padding-top: 0.15rem;
        p{
            height: 0.3rem;
            border-left:1px solid #ccc;
        }
    }
    .red{
        color:#f9446b;
    }
}
.pro-box{
    width: 96%;
    margin: 0 auto;
    display: flex;
    flex-wrap:wrap;
    justify-content:space-between;
    .sma-box{
        border-radius: 0.06rem;
        margin-top: 0.1rem;
        overflow: hidden;
        width: 49%;
        height: 3.78rem;
        box-sizing: border-box;
        font-size: 0.2rem;
        .img-box{
            width: 100%;
            height: 2.9rem;
            overflow: hidden;
            img{
                width: 100%;
            }
        }
        .sale{
            position: relative;
            top:-1.22rem;
            line-height: 0.38rem;
            color: white;
            padding-left:0.18rem;
            height: 0.38rem;
            background:linear-gradient(to right,rgba(0,0,0,0.6), rgba(0,0,0,0)); 
            }
        .title{
            padding: 0.06rem 0.08rem;
            width: 100%;
            height: 0.26rem;
            overflow: hidden;
            margin-bottom: 0.03rem;
            line-height: 0.28rem;
        }
        .love{
            float:right;
            padding-right: 0.25rem;
            margin-right:0.07rem;
            font-size: 0.18rem;
            padding-top:0.05rem; 
            color: #888;
            background: url(data:image/png;base64,) center right no-repeat ;
            background-size: 27% 55%;
        }
        .price{
            padding-left:0.08rem;
            color:#f9446b;
            font-weight: 900;
            font-size: 0.25rem;
        }
    }
}

</style>



