<template>
  <div class="cart-box">
    <div class="top-search-box">
        <i class="iconfont icon-xiangzuojiantou" @click="$router.go(-1)"></i>
        <div  class="title">
            购物车
        </div>
        <div class="right-p">
          <p class="bianji">编辑</p>
          <i class="iconfont icon-xiaoxizhongxin"></i>
        </div>
    </div>
    <div v-if="carts.length==0" class="empty">
        <div class="empty-box">
            <div class="cart-i">
              <i class="iconfont icon-gouwuche"></i>  
            </div>
            <p>购物车竟然是空的~</p>
            <router-link to="/" tag="div" class="gohome">
                去逛逛
            </router-link>
        </div>
    </div>
    <div v-if="carts.length!=0" class="noempty">
        <div class="product-store" v-for="(store,l) in carts" :key="'item'+store">
          <div class="product">
             <div class="store">
              <p class="storename">
                {{Object.keys(store)[0]}}
              </p> 
            </div>
            <div class="product-box" v-for="(item,i) in store[Object.keys(store)]" :key="item">
              <input type="checkbox" @change="changesum()" v-model="tosum" :value="item.id+'-'+item.newprice+'-'+item.count">
              <div class="content-box">
                <div class="img-box">
                  <img :src="item.img" alt="">
                </div>
                <div class="pro-content">
                  <p class="pro-title">{{item.title}}</p>
                  <div class="pro-price">
                    <div class="pro-box">
                        <p class="new-price">￥{{item.newprice}}</p>
                        <p class="old-price">￥{{item.oldprice}}</p>
                    </div>
                    <div class="count">
                      <p @click="count(0,l,Object.keys(store)[0],i)">-</p>
                      <p>{{item.count}}</p>
                      <p  @click="count(1,l,Object.keys(store)[0],i)">+</p>
                    </div>
                  </div>
                </div>
              </div>
            </div>


          </div>
        </div>
    </div>
    <div class="buy" v-if="carts.length!=0">
      <div class="choose-box">
        <input type="checkbox" class="choose" v-model="chooseall">
        <p class="choose-text">全选 （{{allcount}}）</p>
      </div>
      <div class="allprice">¥{{sumprice}}</div>
      <router-link to="/aboutme" tag='div' class="surebuy">去结算</router-link>
    </div>
  </div>
</template>
<script>
import { mapGetters } from "vuex"
export default {
    computed:{
      ...mapGetters([
        "carts"

      ])
    },
    data(){
      return {
        empty:false,
        tosum:[],
        sumprice:0,
        allcount:0,
        chooseall:false


      }
    },
    methods:{
      count(math,storeindex,key,index){
        let cc={
          math,
          storeindex,
          key,
          index
        }
        this.$store.dispatch("math",cc);

      },
      changesum(){
        this.sumprice=0;
        this.allcount=0;
        var sum=0;
        var count=0;
        for(let i=0;i<this.tosum.length;i++){
          var pro=this.tosum[i].split("-");
          sum=pro[1]*pro[2]+sum;
          count=pro[2]*1+count;
        }
        console.log(this.tosum)
        this.sumprice=sum.toFixed(2);
        this.allcount=count;
      }
    },
    mounted(){
      this.$store.dispatch("hiddennav");
    }
}
</script>

<style lang="less" scoped>
  .cart-box{
    width: 100%;
     .top-search-box{
        background: white;
        height: 0.8rem;
        background:white;
        padding-left:0.15rem;
        text-align: center;
        border-bottom: 1px solid #ccc;
        color: #666;
        display: flex;
        align-items: center;
        justify-content:space-between;
        .icon-xiangzuojiantou{
            margin-right:0.13rem; 
        }
        .title{
            font-size: 0.3rem;
            text-align: center; 
            margin-left: 0.3rem;
        }
        .right-p{
          margin-right:0.2rem;
          color:#777;
          display: flex;
          align-items: center;
          .bianji{
            font-size:0.22rem;
            margin-right: 0.1rem;
          }
        }

      }
      .empty{
        width: 100%;
        height: 9.4rem;
        display: flex;
        align-items: center;
        text-align: center;
        .empty-box{
          width: 100%;
          text-align: center;
          .cart-i{
            margin: 0 auto;
            width: 2.2rem;
            height: 2.2rem;
            line-height: 2.2rem;
            background: #d5cbce;
            color: white;
            border-radius: 1.25rem;
            box-shadow: 1px 1px 1px #ccc;
            .iconfont{
              font-size: 1.2rem;
              font-weight: bold;
            }
          }
          p{
            font-size: 0.22rem;
            color:#777;
            margin-top:0.24rem;
          }
          .gohome{
            background: #fb3c64;
            color: white;
            height: 0.6rem;
            width: 1.6rem;
            margin:0.2rem auto;
            border-radius: 0.1rem;
            line-height: 0.6rem;
            font-size: 0.26rem;
          }
        }
      }
      .noempty{
        font-size: 0.2rem;
        width: 100%;
        margin-bottom:0.8rem; 
        .product-store{
          width: 100%;
          border-top:1px solid #ccc;
          margin-bottom:0.16rem; 
          background: white;
          .product{
            width: 100%;
            .store{
              padding-left:0.15rem; 
              height: 0.65rem;
              text-align: left;
              display: flex;
              align-items: center;
              border-bottom: 1px solid #ccc;
              .checkbox-box{
                border-radius:0.15rem; 
                height: 0.65rem;
                line-height: 0.65rem;
                width: 0.5rem;
                overflow: hidden;
                display: flex;
                align-items: center;
                justify-content: center;

                input{
                  width: 0.3rem;
                  height: 0.3rem;
                }
              }
              .storename{
                padding-left:0.04rem;
                font-size: 0.25rem;
                color:#555;
                margin-left:0.1rem 
              }
            }
            .product-box{
              height: 2.4rem;
              border-bottom:1px solid #ccc;
              display: flex;
              align-items: center;
              padding-left:0.24rem;
              input{
                width:0.3rem;
                height: 0.3rem;
              }
              .content-box{
                width: 85%;
                margin-left:0.2rem;
                display: flex;
                align-items: center;
                .img-box{
                  width: 1.2rem;
                  height: 1.6rem;
                  overflow: hidden;
                  border:1px solid #ddd;
                  img{
                    width: 100%
                  }
                }
                .pro-content{
                  height: 1.6rem;
                  margin-left:0.2rem;
                  width: 4rem;
                  .pro-title{
                    width: 95%;
                    padding-top:0.05rem; 
                    max-height: 0.7rem;
                    line-height: 0.3rem;
                    font-weight: 600;
                    font-size: 0.21rem;
                    color:#555;
                  }
                  .pro-price{
                    display: flex;
                    justify-content: space-between;
                    .pro-box{
                      display: flex;
                      align-items: flex-end;
                      .new-price{
                        font-size: 0.28rem;
                        font-weight: 600;
                        color:#fb3c64;
                        margin-left: 0.1rem;
                      }
                      .old-price{
                          font-size:0.18rem;
                          font-weight: 600;
                          text-decoration: line-through;
                          color:#777;
                          margin-left:0.08rem;
                          line-height: 0.35rem;  
                      }
                    }
                    .count{
                      align-items: flex-end;
                      margin-top:0.5rem; 
                      display: flex;
                      margin-right:0.2rem;
                      p{
                        width: 0.4rem;
                        height: 0.34rem;
                        line-height: 0.34rem;
                        text-align: center;
                        border:1px solid #999;
                        margin-left:0.012rem; 
                      }
                    }

                  }

                }
              }
            }
          }
        }
      }
      .buy{
        width: 100%;
        height: 0.8rem;
        background: white;
        position: fixed;
        bottom:0;
        font-size:0.3rem;
        display: flex;
        border-top:1px solid #ccc;
        align-items: center;
        justify-content: space-between;
        .choose-box{
          display: flex;
          align-items: center;
          input{
            margin-left: 0.28rem;
            width: 0.3rem;
            height: 0.3rem;
          }
          .choose-text{
            margin-left:0.1rem;
            font-size: 0.24rem;
            color:#555; 
          }
        }
        .allprice{
            margin-left:0.2rem; 
            color:red;
            font-size: 0.24rem;
            font-weight: 600;
        }
        .surebuy{
          background: #fb3c64;
          color:white;
          font-size: 0.26rem;
          width: 1.6rem;
          height: 0.5rem;
          line-height: 0.5rem;
          text-align: center;
          border-radius: 0.06rem;
          margin-right:0.38rem; 
        }
      }
  }

   

</style>



