const state={
    mycart:[],
    sumprice:0,
}
const getters={
    carts(){
        return state.mycart;  
    },
    sumprice(){
        return state.sumprice
    }
}
const actions={
    addcarts({commit},buycontent){
        commit("addcart",buycontent);
    },
    math({commit},cc){
        commit("math",cc);
    }


}
const mutations={
    addcart(state,buycontent){
        var excstore=false;
        for(let i=0;i<state.mycart.length;i++){
            if(state.mycart[i][buycontent.seller]){
                excstore=true;
                var item=state.mycart[i][buycontent.seller];
                var exc=false;
                for(let j=0;j<item.length;j++){
                    if(item[j].id==(buycontent.id)){
                        item[j].count++;
                        exc=true;
                        return;
                    }
                }
                if(!exc){
                    item.push(buycontent);
                    return;
                }
            }
        }
        if(!excstore){
            let newbuy={};
            let item=newbuy[buycontent.seller]=[];
            item.push(buycontent);
            state.mycart.push(newbuy);
        }
    },
    math(state,{math,storeindex,key,index}){
        console.log( state.mycart[storeindex]);
        if(math==0){
            state.mycart[storeindex][key][index].count--
        }
        if(math==1){
            state.mycart[storeindex][key][index].count++
        }
        if(state.mycart[storeindex][key][index].count<1){
            state.mycart[storeindex][key][index].count=1 
        }
    }
}
export default{
    state,
    getters,
    actions,
    mutations
}


