import Vue from "vue"
const state={
    flag:false,
    history:[],
    hot:[],
    keyword:""
}
const getters={
    history(){
        return state.history;
    },
    hot(){
        return state.hot
    },
    flag(){
        return state.flag;
    },
    hot(){
        return state.hot;
    },
    keyword(){
        return state.keyword;
    }
    

}
const actions={
    tosearch({commit}){
        commit("tosearch");
    },
    leavesearch({commit}){
        commit("leavesearch");
        actions.gethot({commit});

    },
    addhistory({commit},newwords){
        commit("addhistory",newwords);
    },
    cutword({commit}){
        commit("cutword");
    },
    gethot({commit}){
        Vue.http.jsonp("https://list.mogujie.com/module/mget?code=sketch%2ChotWord",
        ).then(({data:{data}})=>{
            commit("gethot",data);
        }).catch((error)=>{
            console.log(error);
        })
    }
    
}
const mutations={
    tosearch(state){
        state.flag=true;
    },
    leavesearch(state){
        state.flag=false;
    },
    addhistory(state,newwords){
        state.history.push(newwords);
    },
    cutword(state){
        state.history=[];
    },
    gethot(state,data){
        state.hot=data.hotWord.data;
        state.keyword=data.sketch.data.query;
    }
}

export default{
    state,
    getters,
    actions,
    mutations
}


