import Vue from "vue"

const state={
    comment:{},
    love:[]
}
const getters={
    comment(){
        return state.comment;
    },
    love(){
        return state.love;
    }
}

const actions={
    getcommit({commit},iid){
        Vue.http.jsonp("https://rate.mogujie.com/h5/rate/jsonp/x6.rate.ratelist/v1",{
            params:{
                itemId:iid
            }
        }).then(({body:{data}})=>{
            console.log(data);
            commit("getcommit",data);
        })
    },
    addlove({commit},id){
        commit("addlove",id);
    }
}

const mutations={
    getcommit(state,data){
        state.comment=data;
    },
    addlove(state,id){
        let iflove=state.love.indexOf(id);
        if(iflove==-1){
            state.love.push(id);
        }else{
            state.love.splice(iflove,1); 
        }

    }
}

export default{
    state,
    getters,
    actions,
    mutations

}


