import Vue from "vue"
const state = {
    banner:[],
    socheaper:[],
    hotmarket:[],
    hottype:[],
    youmaylike:[],
    navflag:true
}
const getters={
    banner(){
        return state.banner
    },
    hotmarket(){
        return state.hotmarket
    },
    socheaper(){
        return state.socheaper
    },
    hottype(){
        return state.hottype
    },
    youmaylike(){
        return state.youmaylike
    },
    navflag(){
        return state.navflag
    }
}
const actions={
    loadbanner({commit}){
        Vue.http.jsonp("https://mce.mogucdn.com/jsonp/multiget/3",{
            params:{
                pids:"51822,51833,51836,106930"
            }
        } ).then(({body:{data}})=>{
            commit("loadbanner",data);
        }).catch(error=>{
            console.log(error)
        })
    },
    loadlikes({commit}){
        Vue.http.jsonp("https://list.mogujie.com/search",{
            params:{
                pid:"9750",
                sort:"pop"
            }
        } ).then(({body:{result:{wall:{list}}}})=>{
            commit("loadlikes",list);
        }).catch(error=>{
            console.log(error)
        })
    },
    hiddennav({commit}){
        commit("hiddennav");
    },
    shownav({commit}){
        commit("shownav")
    }

}
const mutations={
    loadbanner(state,data){
        state.banner=data[51822].list;
        state.socheaper=data[51833].list;
        state.hotmarket=data[51836].list;
        state.hottype=data[106930].list;

    },
    loadlikes(state,list){
        state.youmaylike=list;
    },
    hiddennav(state){
        state.navflag=false
    },
    shownav(state){
        state.navflag=true
    }
}

export default{
    state,
    getters,
    actions,
    mutations
}


