import Vue from "vue";
import Router from "vue-router";
Vue.use(Router);

import HomeComponent from "../components/BodyContent/HomeComponet/HomeComponent.vue"
import TypeComponent from "../components/BodyContent/TypeComponent/TypeComponent.vue"
import CartComponent from "../components/BodyContent/CartComponent/CartComponent.vue"
import AboutMe       from "../components/BodyContent/AboutMe/AboutMe.vue"
import DetailInfo    from "../components/BodyContent/DetailInfo/DetailInfo.vue"


export default new Router({
    mode:"history",
    routes:[
        {
            path:"/",
            name:"home",
            component:HomeComponent
        },{
            path:"/type",
            name:"type",
            component:TypeComponent
        },
        {
            path:"/cart",
            name:"cart",
            component:CartComponent
        },
        {
            path:"/aboutme",
            name:"aboutme",
            component:AboutMe
        },{
            path:"/detail",
            name:"detail",
            component:DetailInfo
        },{
            path:'*',
            redirect : './'
        }
    ]
});


