import Vue from "vue";
const state={
    typetitles:[],
    type:[],
    typeheader:[],
    sortfilter:[],
    products:[],
    productkey:"50675",
}
const getters={
    typetitles(){
        return state.typetitles
    },
    type(){
        return state.type;
    },
    typeheader(){
        return state.typeheader;
    },
    sortfilter(){
        return state.sortfilter;
    },
    products(){
        return state.products;
    },
    productkey(){
        return state.productkey;
    },
    
}
const actions={
    //获取左侧分类标题
    loadtypes({commit}){
        Vue.http.jsonp("https://mce.mogucdn.com/jsonp/multiget/3",{
            params:{
                pids:"41789" 
            }
        }).then(({body:{data:{41789:{list}}}})=>{
            commit("loadtypes",list);
        }).catch((error)=>{
            console.log(erreor)
        })
    },
    // 获取右侧上部分类详情
    lodetypedetail({commit},params={pid:"41863"}){
        Vue.http.jsonp("https://mce.mogujie.com/jsonp/makeup/3",{
                params
        }).then(({body:{data}})=>{
            commit("lodetypedetail",data);
        })
    },
    // 获取分类推荐商品
    lodeproductdetail({commit},params=
        {
            cKey:"h5-cube",
            fcid:"50675",
            sort:""
        }
    ){
        Vue.http.jsonp("https://list.mogujie.com/search",{
            params
        }).then(({body:{result}})=>{
            commit("lodeproductdetail",result);
        })

    }

}
const mutations={
    loadtypes(state,list){
        state.typetitles=list
    },
    lodetypedetail(state,data){
        if(!data.bannerHead){
            state.typeheader=[]
        }else{
            state.typeheader=data.bannerHead.list;
        }
        state.type=data.categoryNavigation.list;
    },
    lodeproductdetail(state,result){
        state.sortfilter=result.sortFilter;
        state.products=result.wall.docs;
        state.productkey=result.param.fcid;
    }

}

export default{
    state,
    getters,
    actions,
    mutations
}


