<template>
    <div class="cheaper-box">
        <p class="cheaper-big-title">超实惠－促销直达</p>
        <div class="cheaper-content-box">
            <div v-for="(item,i) in socheaper " :key="i" :class="'cheaper'+i">
                <p class="cheaper-title">{{item.title}}</p>
                <p class="cheaper-viceTitle">{{item.viceTitle|deletekuo}}</p>
                <img class="cheaper-image" :src="item.image" alt="">
            </div>

        </div>
    </div>
  
</template>
<script>
import {mapGetters} from "vuex"
export default {
    computed:{
        ...mapGetters([
            "socheaper"
        ])
    },
    filters:{
        deletekuo(word){
            return word.substr(1,word.length-2);
        }
    }
}
</script>
<style lang="less" scoped>
    .cheaper-box{
        margin-top: 0.15rem;
        background: white;
        .cheaper-big-title{
            padding-left: 0.28rem;
            font-size: 0.24rem;
            height: 0.56rem;
            line-height: 0.58rem;
            color:#333;
        }
        .cheaper-content-box{
            width: 100%;
            height: 5rem;
            div{
                float: left;
                border-top: 1px solid #ddd;
                text-align: center;
                overflow: hidden;
                box-sizing: border-box;
                .cheaper-title{
                    font-size:0.24rem;
                    font-weight: 800;
                    height: 0.32rem;
                }
                .cheaper-viceTitle{
                    font-size:0.2rem;
                    text-align: center;
                    color:#f9446b;
                }

            }
            .cheaper0{
                border-right:1px solid #ddd;
                width: 50%;
                height: 2.7rem;
                .cheaper-title{
                    margin-top: 0.34rem;
                }
                img{
                    width: 55%;
                }
            }
            .cheaper1,.cheaper2{
                padding-left:0.23rem;
                text-align: left;
                width: 50%;
                height: 1.35rem;
                .cheaper-title{
                   margin-top: 0.28rem;
                }
                .cheaper-image{
                    position: relative;
                    top:-0.8rem;
                    left:58%;
                    height: 1.3rem;
                }
                .cheaper-viceTitle{
                    text-align: left;
                }
            }
            .cheaper3,.cheaper4,.cheaper5{
                width: 33.3%;
                height: 2.25rem;
                img{
                    width: 75%;
                }
                .cheaper-title{
                   margin-top: 0.13rem;
                }
            }
            .cheaper4,.cheaper5{
                border-left:1px solid #ddd;
            }

        }
        
    }
    


</style>




