<template>
    <div class="youmaylike">
        <p class="like-title">猜你喜欢</p>
        <div class="like-big-box">
               <router-link :to="{name:'detail',params:{pro:like}}" tag="div" class="like-box"  v-for="(like,i) in products" :key="i"
            >
                <div class="img-box">
                    <img :src="like.img" alt="">
                </div>
                <p class="wordline">
                    <span  class="word" v-for="(word,i) in like.props" :key="'word'+i">
                        {{word}}
                    </span>
                </p>
                <p class="sale">{{like.sale}}</p>
                <p class="price" >{{like.price}}</p>
               </router-link>

        </div>        
    </div>
  
</template>
<script>
import {mapGetters} from "vuex"
export default {
    computed:{
        ...mapGetters([
            "products"
        ])
    },
    mounted(){
        this.$store.dispatch("lodeproductdetail",{
            cKey:"h5-cube",
            fcid:"10062603",
            sort:""
        });

    }
}
</script>
<style lang="less" scoped>
    .youmaylike{
        margin-bottom: 1.2rem;
        color: #333;
        margin-top: 0.15rem;
        width: 100%;
        .like-title{
            text-align: center;
            font-size: 0.26rem;
            height: 0.75rem;
            line-height: 0.7rem;
            background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAATkAAAAKAgMAAACyFsHVAAAADFBMVEVMaXEzMzMzMzP///8xqFqyAAAAAnRSTlMAtlSR24EAAAABYktHRAMRDEzyAAAAQElEQVQoz2NgoDkQIVZhAHHKMok0jnEJccpWORBnHttK4pStmkCceVJYLV6FCkg1TwtVewPt/Uvt+KB6eqEIAACpih6NjokEAgAAAABJRU5ErkJggg==) no-repeat center center;
            background-size:43% 11%;
        }
        .like-big-box{
            width: 100%;
            display: flex;
            flex-wrap: wrap;
            justify-content:space-around;
            .like-box{
                background: white;
                margin-top:0.15rem; 
                width: 48%;
                height: 5.4rem;
                box-sizing: border-box;
                border-radius: 8px;
                .img-box{
                    width: 100%;
                    height: 4rem;
                    overflow: hidden;
                    img{
                    width: 100%;
                    }
                }
                .wordline{
                    width: 83%;
                    height: 0.7rem;
                    line-height: 0.3rem;
                    padding-top:0.1rem;
                    padding-left: 0.08rem;
                    font-size: 0;
                    overflow: hidden;
                    .word{
                        display: inline-block;
                        background: #ebf0f4;
                        color: #485c66;
                        font-size: 0.15rem;
                        margin-left:0.08rem;
                        padding-left: 0.05rem;
                        padding-right:0.05rem;
                    }
                }
                .price{
                    margin-top:0.1rem;
                    font-size: 0.25rem;
                    font-weight: 900;
                    margin-left:0.08rem;
                }
                .sale{
                    background: url(data:image/png;base64,) center right no-repeat ;
                    background-size: 35% 70%;
                    width: 0.57rem;
                    font-size: 0.1rem;
                    float: right;
                    margin-right:0.16rem;
                    margin-top:0.13rem;


                }
            }
        }
    }

</style>





