<template>
    <div class="type-title-box" ref="cc">
        <div v-for="(type,i) in typetitles" :key="i"  :class="i==cke?'type-active':'' " class="type-box"  @click="loadtypes(type.maitKey,type.miniWallkey,i)">
            <p class="type">{{type.title}}</p>
        </div>
        
    </div>
</template>
<script>
import {mapGetters } from "vuex";
import BScroll from 'better-scroll' 
export default {
    data(){
        return {
           cke:7
        }
    },
    computed:{
      ...mapGetters([
          "typetitles"
      ])
    },
    mounted(){
            this.$store.dispatch("loadtypes");
    },
  methods:{
        loadtypes(obj,key,i){
            // 获取右侧分类
            this.cke=i;
            let params={
                pid:obj
            }
            this.$store.dispatch("lodetypedetail",params);
            // 获取热门商品
            this.loadproduct(key);
        },
        loadproduct(key,sort="pop"){
            let params={
                cKey:"h5-cube",
                fcid:key,
                sort:sort
            }
            this.$store.dispatch("lodeproductdetail",params,key);
        }
  }
}
</script>
<style lang="less" scoped>
    .type-title-box{
        width: 23%;
        box-sizing: border-box;
        height: 9.673rem;
        overflow: hidden;
        overflow-y: scroll;
        .type-box{
            width: 100%;
            height: 0.5rem;
            line-height: 0.5rem;
            font-size: 0.245rem;
            color:#555;
            text-align: center;
            padding-top:0.25rem;
            .type{
                color:#555;
                height: 0.25rem;
                line-height: 0.25rem;
            }
        }
        .type-active{
            background: white;
            .type{
                color:#f9446b;
                border-left:3px solid #f9446b;
                height: 0.25rem;
                line-height: 0.25rem;
            }
        }
    }

</style>





