<template>
    <div class="hotbox">
        <div class="typebox" v-for="(hottype,i) in hottype" :key="i">
            <img :src="hottype.image" class="typeimg">
            <p>{{hottype.title}}</p>
        </div>
    </div>
  
</template>
<script>
import {mapGetters} from "vuex"
export default {
    computed:{
        ...mapGetters([
            "hottype"
        ])
    }
}
</script>
<style lang="less" scoped>
    .hotbox{
        width: 100%;
        height: 3.1rem;
        background: white;
        display: flex;
        flex-wrap: wrap;
        padding-top: 0.27rem;
            .typebox{
                width: 20%;
                height:1.45rem;
                box-sizing:border-box;
                text-align: center;
                font-size: 0.2rem;
                color:#555;
                .typeimg{
                    width: 75%;
                }
            }
    }
    
</style>




