<template>
    <div class="searchpage" v-if="flag">
        <div class="top-search-box">
        <i class="iconfont icon-xiangzuojiantou" @click="leavesearch()"></i>
        <input type="text" class="search" :class="pink?'pink':''" @blur="toblack()" @focus="topink()" v-model="newwords" :placeholder="keyword">
        <span class="textsearch" @click="givewords()">搜索</span>
        </div>
        <div class="search-box">
            <p class="search-title">
                <i class="icon iconfont icon-sousuo"></i>
                <span>历史搜索</span>
                <i class="icon iconfont icon-lajixiang" @click="cutword()"></i>
            </p>
            <div class="words">
                <p class="noword" v-if=" history.length==0 ">暂无历史搜索</p>
                <p class="word" v-for="(item,i) in history" :key="i">{{item}}</p>
            </div>
         
        </div>
        <div class="search-box">
            <p class="search-title">
                <i class="icon iconfont icon-shoucang"></i>
                <span>热门搜索</span> 
            </p>
            <div class="words">
                <p class="word" v-for="(word,i) in hot" :key="'hot'+i" :style="'color:'+word.color">{{word.frontword}}</p>
            </div> 

        </div>
    </div>
</template>
<script>
import {mapGetters}from "vuex"
export default {
    data(){
        return {
            pink:false,
            newwords:""
        }
    },
    computed:{
        ...mapGetters([
            "flag",
            "history",
            "hot",
            "keyword"
        ])
    },
    methods:{
        leavesearch(){
            this.$store.dispatch("leavesearch");
        },
        topink(){
            this.pink=true;
        },
        toblack(){
            this.pink=false;
        },
        givewords(){
            this.$store.dispatch("addhistory",this.newwords);
        },
        cutword(){
            this.$store.dispatch("cutword");
        }
    },
    mounted(){
            this.$store.dispatch("gethot");
    }
}
</script>
<style lang="less" scoped>
.searchpage{
    position:fixed;
    top:0rem;
    width: 100%;
    height: 100%;
    z-index:9999;
    background:white;
    .top-search-box{
        background: white;
        font-size: 0.24rem;
        width: 100%;
        height: 0.78rem;
        text-align: center;
        color: #666;
        display: flex;
        align-items: center;
        justify-content:space-around;
        .icon-xiangzuojiantou{
            margin-left:0.13rem; 
        }
        .search{
            outline: none;
            border: none;
            width: 70%;
            height: 0.39rem;
            border:1px solid #888;
            border-radius: 5px;
            line-height: 0.39rem;
            padding-left: 0.2rem;
        }
        input::placeholder{
            color:#aaa;
            font-size: 0.24rem;
        }
        .pink{
            border:1px solid #f9446b;
        }
        .textsearch{
            margin-right:0.15rem;
        }
    }
    .search-box{
        border-top: 1px solid #eee;
        font-size: 0.22rem;
        color:#999;
        .search-title{
            height: 0.5rem;
            line-height: 0.45rem;
            padding: 0.15rem 0.25rem;
            .icon-lajixiang{
                float:right;
                font-size: 0.28rem;
                margin-right: 0.13rem;
                
            }
            i{
                font-size: 0.26rem;
            }    
        }
        .words{
            padding:0.02rem 0.5rem;
            margin-bottom: 0.28rem;
            display: flex;
            width: 90%;
            flex-wrap: wrap;
            .word{
                font-size: 0.21rem;
                height: 0.26rem;
                margin:0.08rem 0.1rem;
                color:#666;
                padding: 0.05rem 0.15rem;
                border:1px solid #eaeaea;
                border-radius:0.08rem;
            }
            .noword{
                padding-left:0.1rem; 
            }

        }
        

    }
}
 

</style>





